
const LANG_KEY = 'currentLang'; //  
const GENRES_KEY = 'genres'; //  
const PAGE_KEY = 'page'; //  
const TOTAL_KEY = 'total_pages'; //    
const CURRENT_KEY = 'currentPage'; //   
const WATCHED_KEY = 'wathedArray'; //   
const QUEUE_KEY = 'queueArray'; //     

//    try/catch
const save = (key, value) => {
    try {
        const jsonData = JSON.stringify(value);
        return localStorage.setItem(key, jsonData);
    }
    catch {
        Notiflix.Notify.failure(error.message);
        
    }
}
const load = key => {
    try {
        const jsonData = localStorage.getItem(key);
        return  jsonData === null ? undefined : JSON.parse(jsonData);
         
    }
    catch {
        Notiflix.Notify.failure(error.message);
    }
}
const remove = key => {
    try {
        return localStorage.removeItem(key);
    }
    catch {
     Notiflix.Notify.failure(error.message);   
    }
}
//    // localStorage
const saveLang = (curLang)=> {
    save(LANG_KEY, curLang);
}
const loadLang = () => {
    return load(LANG_KEY);
}
const saveGenres = (arrGenres) => {
     save(GENRES_KEY,arrGenres)
}
const loadGenres = () => {
    return load(GENRES_KEY);
 }
const initialLocal = () => {
    if (!load(CURRENT_KEY)) save(CURRENT_KEY, []);
    if (!load(WATCHED_KEY)) save(WATCHED_KEY, []);
    if (!load(QUEUE_KEY)) save(QUEUE_KEY, []);
    }
const savePage = page => { // 
     save(PAGE_KEY,page);
}
const loadPage = () => {  // 
    return load(PAGE_KEY);
}
const saveTotalPages = (number) => { //  
    save(TOTAL_KEY, number);
}
const loadTotalPages = () => {  //  
    return load(TOTAL_KEY);
}  
const saveCurrentPage = (movies) => { //    
    save(CURRENT_KEY, movies);
}
const loadCurrentPage = () => { //    
    return load(CURRENT_KEY);
}
function saveToWatched(movies) {
    save(WATCHED_KEY, movies);
}
function loadFromWatched() {
    return load(WATCHED_KEY);
}
const saveToQueue = (movies) => {
    save(QUEUE_KEY, movies);
}
function loadFromQueue() {
  return  load(QUEUE_KEY);
   
}


    
export default {
    save,
    load,
    remove,
    saveLang,
    loadLang,
    saveGenres,
    loadGenres,
    initialLocal,
    savePage,
    loadPage,
    saveTotalPages,
    loadTotalPages,
    saveCurrentPage,
    loadCurrentPage,
    saveToWatched,
    loadFromWatched,
    saveToQueue,
    loadFromQueue,
    }